// axios
import theaxios from 'axios'
import router from '@/router'
import Dialog from '@/utils/Dialog.js'
import { getToken, removeToken, setToken } from '../utils/token.js'
import { getNewTokenAPI } from '@/api/index.js'
// axios
const axios = theaxios.create({
  baseURL: 'http://geek.itheima.net',
  timeout: 20000// 20
})

// 
// ?. length0undefiend
axios.interceptors.request.use(function (config) {
  if (getToken()?.length > 0 && config.headers.Authorization === undefined) {
    config.headers.Authorization = `Bearer ${getToken()}`
  }
  // 
  return config
}, function (error) {
  // 
  return Promise.reject(error)
})

// 
axios.interceptors.response.use(function (response) {
  // http2xx3xx
  // 
  return response
}, async function (error) {
  // http4xx5xx
  // 401
  if (error.response.status === 401) { 
    // 1, 
    // // this.$router.replace('/login),nodethis
    // router.replace('/login')
 
    // 2token,
    try {
    const res = await getNewTokenAPI()// token
    // 1.token
    setToken(res.data.data.token)
    // 2.token
    error.config.headers.Authorization = `Bearer ${res.data.data.token}`
    // 3.
      return axios(error.config)
    } catch (err) {
      router.replace(`/login?path=${router.currentRoute.fullPath}`)// 
      removeToken()// token
      Dialog.alert({
        message: ''
      })
      // router.currentRoute
    }
  } else if (error.response.status === 500 && error.config.url === '/v1_0/authorizations' && error.config.method === 'put') {
    // refresh_token
    removeToken()
    router.replace('/login')
  } else { return Promise.reject(error) }
  // : token
// :
// 1. localStoragegeektoken()
// 2. /(token)
// 3. ,  401, 

// 401
// 1: token, , , token
// ,  -> 
// 2: token, refresh_token, , 
// token, ,  -> 
// 1. , localStorage.setItem('refresh_token', refresh_token)
// 2. 401, login, tokenapi
// 3. token
// 4. errorheaderstoken
// 5. axios, Promise
// : token, refresh_token500

// 401+500
// tokenrefresh_token (, 2token)
// 

  // 
})

//  axios 
export default ({ url, method = 'GET', params = {}, data = {}, headers = {} }) =>
  axios({
    url,
    method,
    params,
    data,
    headers
  })
