import axios from '../utils/request'
export * from './AriicleDetail.js'// 
export * from './User'
// 
// axios(headers)Content-Type:application/json
export const loginAPI = ({ mobile, code }) =>
  axios({
    url: '/v1_0/authorizations',
    method: 'POST',
    data: {
      mobile,
      code
    }
  })

// 
export const getAllChannelsAPI = () =>
  axios({
    url: '/v1_0/channels',
    method: 'GET'
  })

// 
export const getUserChannelsAPI = () =>
  axios({
    url: '/v1_0/user/channels',
    method: 'GET'

  })
// --
export const updateChannelsAPI = ({ channels }) =>
  axios({
    url: '/v1_0/user/channels',
    method: 'PUT',
    data: { channels }
  })
// id
export const removeChannelAPI = (id) =>
  axios({
    url: `/v1_0/user/channels/${id}`, // node.js :paramsquery
    method: 'DELETE'
  })

// 
export const suggestListAPI = ({ keyword }) =>
  axios({
    url: '/v1_0/suggestion',
    method: 'GET',
    params: {
      q: keyword
    }
  })

// 
export const searchResultAPI = ({ page = 1, per_page = 10, q }) =>
  axios({
    url: '/v1_0/search',
    method: 'GET',
    params: {
      page,
      per_page,
      q
    }
  })
