import axios from '../utils/request'
// 
export const getAllarticleListAPI = ({
  channel_id,
  timestamp
}) =>
  axios({
    url: '/v1_0/articles',
    method: 'GET',

    params: {
      channel_id,
      timestamp
    }
  })
// 
export const deislikeArticleAPI = ({ target }) =>
  axios({
    url: '/v1_0/article/dislikes',
    method: 'POST',
    data: {
      target
    }
  })
// 
export const reportsArticelAPI = ({ target, type, remark }) =>
  axios({
    url: '/v1_0/article/reports',
    method: 'POST',

    data: {
      target, type, remark
    }
  })
// -
export const detailArticeAPI = ({ id }) =>
  axios({
    url: `/v1_0/articles/${id}`,
    method: 'GET'
  })
  // ---
export const praiseArticleAPI = ({ id }) =>
  axios({
    url: '/v1_0/article/likings',
    method: 'POST',
    data: {
      target: id
    }
    })
// --
export const unpraiseArticleAPI = ({ id }) =>
  axios({
    url: `/v1_0/article/likings/${id}`,
    method: 'DELETE'
  })
// --
export const commentsListAPI = ({ type = 'a', id, offset = null }) =>
  axios({
    url: '/v1_0/comments',
    method: 'GET',
    params: {
      type,
      source: id,
      offset
    }
})  

// -- /v1_0/comment/likings
export const commentLikeAPI = ({ id }) =>
  axios({
    url: '/v1_0/comment/likings',
    method: 'POST',
    data: {
      target: id
    }
  })
  // --

export const uncommentLikeAPI = ({ id }) =>
  axios({
    url: `/v1_0/comment/likings/${id}`,
    method: 'DELETE'
  })

  // --
export const publishACommentAPI = ({
  target,
  content,
  art_id = null
  // data()
}) => {
  const data = {
    target,
    content
  }
  if (art_id !== null) {
    data.art_id = art_id
  }
  return axios({
    url: '/v1_0/comments',
    method: 'POST',
    data
  })
}
